@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhai+2:wght@400;700&display=swap);
* {
    box-sizing: border-box;
    font-family: 'Baloo Bhai 2', cursive;
}

body {
    background-color: #eee;
}

.movies {
    width: 1300px;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
}

.movie {
    width: 600px;
    padding: 15px;
    background-color: #fff;
    border: 1px solid #eee;
    transition: ease-in-out 0.2s;
    border-radius: 10px;
    margin: 10px;
}

.movie:hover{
    transform: translateY(-7px);
    box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.3);
}

.movie img {
    width: 30%;
    margin: 25px;
}

.movie_title {
    text-align: center;
    font-size: 26px;
    margin-bottom: 0;
}

.movie_description {
    position: relative;
    top: -50px;
    display: inline-block;
    width: 48%;
}

.movie_genres {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding: 0;
}

.genre {
    width: 33%;
    justify-content: space-between;
    color: gray;
}

